	module.exports = {"data":{"postsJson":{"title":"Challenged By Religious Leaders","questionsTitle":"COMMANDS & PROMISES","display":"block","bookName":"Mark","ref":"11:27-13:37","html":"<p>   <strong>11</strong><u>27</u>They came again to Jerusalem, and as he was walking in the temple, the chief priests, and the scribes, and the elders came to him, <u>28</u>and they began saying to him,</p><h1>JEWISH LEADERS</h1><h4>By what authority do you do these things? Or who gave you this authority to do these things?</h4><p>   <u>29</u>Jesus said to them,</p><h1>JESUS</h1><h2>I will ask you one question. Answer me, and I will tell you by what authority I do these things. <u>30</u>The baptism of John—was it from heaven, or from men? Answer me.</h2><p>   <u>31</u>They reasoned with themselves, saying,</p><h1>JEWISH LEADERS</h1><h4>If we should say, ‘From heaven;’ he will say, ‘Why then did you not believe him?’ <u>32</u>If we should say, ‘From men’</h4><p>—they feared the people, for all held John to really be a prophet. <u>33</u>They answered Jesus,</p><h1>JEWISH LEADERS</h1><h4>We don’t know.</h4><p>   Jesus said to them,</p><h1>JESUS</h1><h2>Neither do I tell you by what authority I do these things.</h2><p>   <strong>12</strong><u>1</u>He began to speak to them in parables.</p><h1>JESUS</h1><h2>A man planted a vineyard, put a hedge around it, dug a pit for the wine press, built a tower, rented it out to a farmer, and went into another country. <u>2</u>When it was time, he sent a servant to the farmer to get from the farmer his share of the fruit of the vineyard. <u>3</u>They took him, beat him, and sent him away empty. <u>4</u>Again, he sent another servant to them; and they threw stones at him, wounded him in the head, and sent him away shamefully treated. <u>5</u>Again he sent another; and they killed him; and many others, beating some, and killing some. <u>6</u>Therefore still having one, his beloved son, he sent him last to them, saying, ‘They will respect my son.’ <u>7</u>But those farmers said among themselves, ‘This is the heir. Come, let’s kill him, and the inheritance will be ours.’ <u>8</u>They took him, killed him, and cast him out of the vineyard. <u>9</u>What therefore will the lord of the vineyard do? He will come and destroy the farmers, and will give the vineyard to others. <u>10</u>Haven’t you even read this Scripture: <br />   ‘The stone which the builders rejected, <br />   the same was made the head of the corner. <br />   <u>11</u>This was from the Lord, <br />   it is marvelous in our eyes’?*</h2><p>   <u>12</u>They tried to seize him, but they feared the multitude; for they perceived that he spoke the parable against them. They left him, and went away. <u>13</u>They sent some of the Pharisees and of the Herodians to him, that they might trap him with words. <u>14</u>When they had come, they asked him,</p><h1>THE PHARISEES &amp; THE HERODIANS</h1><h4>Teacher, we know that you are honest, and don’t defer to anyone; for you aren’t partial to anyone, but truly teach the way of God. Is it lawful to pay taxes to Caesar, or not? <u>15</u>Shall we give, or shall we not give?</h4><p>   But he, knowing their hypocrisy, said to them,</p><h1>JESUS</h1><h2>Why do you test me? Bring me a denarius, that I may see it.</h2><p>   <u>16</u>They brought it. <br />   He said to them,</p><h1>JESUS</h1><h2>Whose is this image and inscription?</h2><p>   They said to him,</p><h1>THE PHARISEES &amp; THE HERODIANS</h1><h4>Caesar’s.</h4><p>   <u>17</u>Jesus answered them,</p><h1>JESUS</h1><h2>Render to Caesar the things that are Caesar’s, and to God the things that are God’s.</h2><p>   They marveled greatly at him. <br />   <u>18</u>There came to him Sadducees, who say that there is no resurrection. They asked him, saying,</p><h1>THE SADDUCEES</h1><h4><u>19</u>Teacher, Moses wrote to us, ‘If a man’s brother dies, and leaves a wife behind him, and leaves no children, that his brother should take his wife, and raise up offspring for his brother.’ <u>20</u>There were seven brothers. The first took a wife, and dying left no offspring. <u>21</u>The second took her, and died, leaving no children behind him. The third likewise; <u>22</u>and the seven took her and left no children. Last of all the woman also died. <u>23</u>In the resurrection, when they rise, whose wife will she be of them? For the seven had her as a wife.</h4><p>   <u>24</u>Jesus answered them,</p><h1>JESUS</h1><h2>Isn’t this because you are mistaken, not knowing the Scriptures, nor the power of God? <u>25</u>For when they will rise from the dead, they neither marry, nor are given in marriage, but are like angels in heaven. <u>26</u>But about the dead, that they are raised; haven’t you read in the book of Moses, about the Bush, how God spoke to him, saying, ‘I am the God of Abraham, the God of Isaac, and the God of Jacob’*? <u>27</u>He is not the God of the dead, but of the living. You are therefore badly mistaken.</h2><p>   <u>28</u>One of the scribes came, and heard them questioning together. Knowing that he had answered them well, asked him,</p><h1>A RELIGIOUS TEACHER</h1><h4>Which commandment is the greatest of all?</h4><p>   <u>29</u>Jesus answered,</p><h1>JESUS</h1><h2>The greatest is, ‘Hear, Israel, the Lord our God, the Lord is one: <u>30</u>you shall love the Lord your God with all your heart, and with all your soul, and with all your mind, and with all your strength.’* This is the first commandment. <u>31</u>The second is like this, ‘You shall love your neighbor as yourself.’* There is no other commandment greater than these.</h2><p>   <u>32</u>The scribe said to him,</p><h1>A RELIGIOUS TEACHER</h1><h4>Truly, teacher, you have said well that he is one, and there is none other but he, <u>33</u>and to love him with all the heart, and with all the understanding, with all the soul, and with all the strength, and to love his neighbor as himself, is more important than all whole burnt offerings and sacrifices.</h4><p>   <u>34</u>When Jesus saw that he answered wisely, he said to him,</p><h1>JESUS</h1><h2>You are not far from God’s Kingdom.</h2><p>   No one dared ask him any question after that. <u>35</u>Jesus responded, as he taught in the temple,</p><h1>JESUS</h1><h2>How is it that the scribes say that the Christ is the son of David? <u>36</u>For David himself said in the Holy Spirit, <br />   ‘The Lord said to my Lord, <br />   Sit at my right hand, <br />   until I make your enemies the footstool of your feet.”’* <br />   <u>37</u>Therefore David himself calls him Lord, so how can he be his son?</h2><p>   The common people heard him gladly. <u>38</u>In his teaching he said to them,</p><h1>JESUS</h1><h2>Beware of the scribes, who like to walk in long robes, and to get greetings in the marketplaces, <u>39</u>and the best seats in the synagogues, and the best places at feasts: <u>40</u>those who devour widows’ houses, and for a pretense make long prayers. These will receive greater condemnation.</h2><p>   <u>41</u>Jesus sat down opposite the treasury, and saw how the multitude cast money into the treasury. Many who were rich cast in much. <u>42</u>A poor widow came, and she cast in two small brass coins,* which equal a quadrans coin.* <u>43</u>He called his disciples to himself, and said to them,</p><h1>JESUS</h1><h2>Most certainly I tell you, this poor widow gave more than all those who are giving into the treasury, <u>44</u>for they all gave out of their abundance, but she, out of her poverty, gave all that she had to live on.</h2><p>   <strong>13</strong><u>1</u>As he went out of the temple, one of his disciples said to him,</p><h1>ONE DISCIPLE</h1><h3>Teacher, see what kind of stones and what kind of buildings!</h3><p>   <u>2</u>Jesus said to him,</p><h1>JESUS</h1><h2>Do you see these great buildings? There will not be left here one stone on another, which will not be thrown down.</h2><p>   <u>3</u>As he sat on the Mount of Olives opposite the temple, Peter, James, John, and Andrew asked him privately,</p><h1>FOUR DISCIPLES</h1><h3><u>4</u>Tell us, when will these things be? What is the sign that these things are all about to be fulfilled?</h3><p>   <u>5</u>Jesus, answering, began to tell them,</p><h1>JESUS</h1><h2>Be careful that no one leads you astray. <u>6</u>For many will come in my name, saying, ‘I am he!*’ and will lead many astray. <br />   <u>7</u>When you hear of wars and rumors of wars, don’t be troubled. For those must happen, but the end is not yet. <u>8</u>For nation will rise against nation, and kingdom against kingdom. There will be earthquakes in various places. There will be famines and troubles. These things are the beginning of birth pains. <u>9</u>But watch yourselves, for they will deliver you up to councils. You will be beaten in synagogues. You will stand before rulers and kings for my sake, for a testimony to them. <u>10</u>The Good News must first be preached to all the nations. <u>11</u>When they lead you away and deliver you up, don’t be anxious beforehand, or premeditate what you will say, but say whatever will be given you in that hour. For it is not you who speak, but the Holy Spirit. <br />   <u>12</u>Brother will deliver up brother to death, and the father his child. Children will rise up against parents, and cause them to be put to death. <u>13</u>You will be hated by all men for my name’s sake, but he who endures to the end, the same will be saved. <u>14</u>But when you see the abomination of desolation,* spoken of by Daniel the prophet, standing where it ought not </h2><p>(let the reader understand),</p><h1>JESUS</h1><h2>then let those who are in Judea flee to the mountains, <u>15</u>and let him who is on the housetop not go down, nor enter in, to take anything out of his house. <u>16</u>Let him who is in the field not return back to take his cloak. <u>17</u>But woe to those who are with child and to those who nurse babies in those days! <u>18</u>Pray that your flight won’t be in the winter. <u>19</u>For in those days there will be oppression, such as there has not been the like from the beginning of the creation which God created until now, and never will be. <u>20</u>Unless the Lord had shortened the days, no flesh would have been saved; but for the sake of the chosen ones, whom he picked out, he shortened the days. <u>21</u>Then if anyone tells you, ‘Look, here is the Christ!’ or, ‘Look, there!’ don’t believe it. <u>22</u>For there will arise false christs and false prophets, and will show signs and wonders, that they may lead astray, if possible, even the chosen ones. <u>23</u>But you watch. <br />   Behold, I have told you all things beforehand. <u>24</u>But in those days, after that oppression, the sun will be darkened, the moon will not give its light, <u>25</u>the stars will be falling from the sky, and the powers that are in the heavens will be shaken.* <u>26</u>Then they will see the Son of Man coming in clouds with great power and glory. <u>27</u>Then he will send out his angels, and will gather together his chosen ones from the four winds, from the ends of the earth to the ends of the sky. <br />   <u>28</u>Now from the fig tree, learn this parable. When the branch has now become tender, and produces its leaves, you know that the summer is near; <u>29</u>even so you also, when you see these things coming to pass, know that it is near, at the doors. <u>30</u>Most certainly I say to you, this generation* will not pass away until all these things happen. <u>31</u>Heaven and earth will pass away, but my words will not pass away. <u>32</u>But of that day or that hour no one knows, not even the angels in heaven, nor the Son, but only the Father. <u>33</u>Watch, keep alert, and pray; for you don’t know when the time is. <br />   <u>34</u>It is like a man, traveling to another country, having left his house, and given authority to his servants, and to each one his work, and also commanded the doorkeeper to keep watch. <u>35</u>Watch therefore, for you don’t know when the lord of the house is coming, whether at evening, or at midnight, or when the rooster crows, or in the morning; <u>36</u>lest coming suddenly he might find you sleeping. <u>37</u>What I tell you, I tell all: Watch.</h2><h5>*Mark 12:11 Psalm 118:22-23 <br />*Mark 12:26 Exodus 3:6 <br />*Mark 12:30 Deuteronomy 6:4-5 <br />*Mark 12:31 Leviticus 19:18 <br />*Mark 12:36 Psalm 110:1 <br />*Mark 12:42 literally, lepta (or widow’s mites). Lepta are very small brass coins worth half a quadrans each, which is a quarter of the copper assarion. Lepta are worth less than 1% of an agricultural worker’s daily wages. <br />*Mark 12:42 A quadrans is a coin worth about 1/64 of a denarius. A denarius is about one day’s wages for an agricultural laborer. <br />*Mark 13:6 or, “I AM!” <br />*Mark 13:14 Daniel 9:17; 11:31; 12:11 <br />*Mark 13:25 Isaiah 13:10; 34:4 <br />*Mark 13:30 The word translated “generation” (genea) could also be translated “race”, “family”, or “people”. </h5>","path":"/322","Question1":"What God-given guidance can you observe in this passage?","Question2":"Put yourself in the passage and envision what it might have been like to receive this guidance. How does this relate to you and your family today?","Question3":"What God-inspired encouragement can you observe in this passage? How can you experience more of this in your own life?","Question4":"Write a poem about what this encouragement means for you.","prevPath":"/321","nextPath":"/323"}},"pathContext":{}}

